.toolbar {
  height: 56px;
  width: 100%;
  position: fixed;
  top: 0;
  left: 0;
  background: #211D1E;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: justify;
      justify-content: space-between;
  -ms-flex-align: center;
      align-items: center;
  /*padding: 0 20px;*/
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  z-index: 90;
}

.toolbar nav {
  height: 100%;
}

.logo {
  height: 80%;
  margin: auto 20px;
}

@media (max-width: 499px) {
  .desktopOnly {
    display: none;
  }
}



