.buildControls {
  width: 100%;
  background: #DDD0BE;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-align: center;
      align-items: center;
  -webkit-box-shadow: 0 2px 1px #ccc;
          box-shadow: 0 2px 1px #ccc;
  margin: auto;
  padding: 10px 0;
}


.orderButton {
  background-color: #211D1E;
  margin-top: 16px;
  border: none;
  outline: none;
  cursor: pointer;
  color: #F5B515;
  font-family: inherit;
  font-size: 1.2em;
  padding: 15px 30px;
  -webkit-box-shadow: 0 20px 25px -5px rgba(0, 0, 0, 0.1), 0 10px 10px -5px rgba(0, 0, 0, 0.04);
          box-shadow: 0 20px 25px -5px rgba(0, 0, 0, 0.1), 0 10px 10px -5px rgba(0, 0, 0, 0.04);
}

.orderButton:hover, .orderButton:active {
  background-color: #F5B515;
  color: #211D1E;
}

.orderButton:disabled {
  background-color: #C7C6C6;
  cursor: not-allowed;
  border: 1px solid #C7C6C6;
  color: #888888;
}

.orderButton:not(:disabled) {
  -webkit-animation: enable 0.3s linear;
          animation: enable 0.3s linear;
}

@-webkit-keyframes enable {
  0% {
      -webkit-transform: scale(1);
              transform: scale(1);
  }
  60% {
      -webkit-transform: scale(1.1);
              transform: scale(1.1);
  }
  100% {
      -webkit-transform: scale(1);
              transform: scale(1);
  }
}

@keyframes enable {
  0% {
      -webkit-transform: scale(1);
              transform: scale(1);
  }
  60% {
      -webkit-transform: scale(1.1);
              transform: scale(1.1);
  }
  100% {
      -webkit-transform: scale(1);
              transform: scale(1);
  }
}



